import React, { Component, PropTypes } from 'react'
import { connect } from 'react-redux'
import {resource} from '../../actions'

export function init(){
    return (dispatch) => {
        resource ('GET', headlines)
        .then((response) =>{
            dispatch({type: 'TO_MAIN_PAGE'})
            dispatch({tyep: 'UPDATE_STATUS', username:response.headlines[0].username, headline:response.headlines[0].headline})
        })
        .catch((err) =>{
            console.log(err)
        })
    }
}

export function LoginAction({username, password}){
    return (dispatch) =>{
        resource('POST', 'login', {username, password})
        .then((response)=>{
            dispatch({type: 'LOG_IN', username: response.username})
            dispatch(init())
        })
        .catch((err) => {
            //console.log(err)
        })
    }
}
