import React from 'react'
import {connect} from 'react-redux'
import { updateHeadline } from '../profile/profileActions'
const Headline = ({user, uptStatus}) =>{
    console.log(user)
    return(
        <div className='headline'>
            <img src='images/evita.jpg'
                alt='image' className='img-responsive center-block profileImage'/>
            <label className='displayName'> {user.name} </label>
            <div id='status' className='status'> {user.status}</div>
                <input id='newStatus' type='text' className="form-control" placeholder='New Status' aria-describedby="pencil-addOn" />
            <div>
            <button type='button' className='btn' onClick={() => 
                {updateStatus(uptStatus)
                document.getElementById('newStatus').value = ''
                }}>Update Status</button>
            </div>
        </div>
    )
}

const updateStatus = (uptStatus) =>{
    let newStatus = document.getElementById('newStatus')
    let status = document.getElementById('status')
    if(newStatus.value != '' ){
        uptStatus(newStatus.value) 
    }
}

export default connect(
    (state) => {
        return {
            user: state.user
        }
    },
    (dispatch) =>{
        return{
            uptStatus : (status) => dispatch({type: 'UPDATE_STATUS', status}),
        }
    }

)(Headline)



